import type { MoInterfaces } from '@ali/4ever-cangjie';

export type BorderStyle = 'none' | 'hidden' | 'single' | 'dotted' | 'dashed' | 'solid' | 'double' | 'groove' | 'ridge' | 'inset' | 'outset';

export type VerticalAlign = 'bottom' | 'center' | 'top' | 'both';

export type TextDirection = 'lrTb' | 'tbRl' | 'btLr' | 'lrTbV' | 'tbRlV' | 'tbLrV';

export enum TableTag {
  Table = 'table',
  Tr = 'tr',
  Tc = 'tc',
}

export interface BorderProperties {
  /**
   * 边框类型
   */
  val?: BorderStyle;

  /**
   * 边框厚度
   */
  sz?: number;

  /**
   * 边框颜色
   */
  color?: string;

  /**
   * 边框间距尺寸
   */
  space?: number;

  /**
   * 边框阴影
   */
  shadow?: boolean;

  /**
   * 创建边框效果
   */
  frame?: boolean;
}

export interface TableCellBorderProperties {
  /**
   * 上边框
   */
  top?: BorderProperties;

  /**
   * 右边框
   */
  right?: BorderProperties;

  /**
   * 下边框
   */
  bottom?: BorderProperties;

  /**
   * 左边框
   */
  left?: BorderProperties;

  /**
   * 内部水平边缘边框
   */
  insideH?: BorderProperties;

  /**
   * 内部垂直边缘边框
   */
  insideV?: BorderProperties;
}

/**
 * shd 文字底纹
 * @module Text
 */
export interface ShadowProperties {
  /**
   * 指定中断符的类型，它可以确定下文文本的位置如何放置
   *
   * @defaultValue paragraph shadow
   */
  val: string;

  fill: string;

  color: string;
}

export interface TableCellProperties {
  /**
   * 单元格宽度
   */
  width?: string;

  /**
   * 单元格横跨列数
   */
  vMerge?: string;

  /**
   * 单元格横跨列数
   */
  colSpan: number;

  /**
   * 单元格横跨行数
   */
  rowSpan: number;

  hidden?: boolean;

  /**
   * 单元格边框
   *
   */
  bdr?: TableCellBorderProperties;

  /**
   * 单元格填充色
   */
  fill?: string;

  shd?: ShadowProperties;

  /**
   * 单元格适应文字
   */
  fitText?: boolean;

  /**
   * 单元格垂直对齐方式
   *
   */
  vAlign?: VerticalAlign;

  /**
   * 单元格文本方向
   */
  textDirection?: TextDirection;
}

export interface TableRowProperties {
  /**
   * 表格单行高度
   */
  h?: number;

  /**
   * 表格单行高度规则
   */
  hRule?: string;

  /**
   * 跨行段页
   */
  cantSplit?: boolean;

  /**
   * 在个页面顶端标题行重复出现
   */
  tblHeader?: boolean;

  /**
   * 是否为表头行（tblHeader 是 Word 数据结构，不复用此字段，避免数据兼容问题）
   */
  isTblHeader?: boolean;
}

export interface TableProperties {

  /**
   * 列宽单位，默认 px
   */

  tblW?: MoInterfaces.TblW.TblW;

  /**
   * 列宽定义
   */
  colsWidth: number[];

  /**
   * 表格对齐方式
   * start == left end = right
   */
  jc: 'left' | 'center' | 'right' | 'both' | 'distribute' | 'start' | 'end';
}
